import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import initializeApp from '../Firebase'
import ShowTable from './ShowTable'
import {
  collection,
  query,
  getFirestore,
  where,
  getDocs,
  orderBy,
} from 'firebase/firestore'
import { useStoreContext } from '../StoreContext'
import { useParams } from 'react-router-dom'

function AllProjects() {
  const { storename: storeName } = useParams()
  const { setStoreName } = useStoreContext()
  setStoreName(storeName)

  const db = getFirestore(initializeApp)
  const dbRef = collection(db, 'AgingPlans')
  const q = query(dbRef, where('place', '==', storeName), orderBy('startDate'))

  const [dataArray, setDataArray] = useState([{}])
  const handleButtonClick = async () => {
    GetData(q, setDataArray)
  }

  return (
    <StyledBorder>
      <StyledButton onClick={handleButtonClick}>조회하기</StyledButton>
      {/* <h2>{storeName}</h2> */}
      <h3>
        <br /> 현재 자동으로 읽어오지않고 있습니다 ㅠㅠ <br />
        버튼을 클릭해서 조회해주세요
      </h3>
      {<ShowTable data={dataArray} onRefresh={handleButtonClick}></ShowTable>}
    </StyledBorder>
  )
}

export default AllProjects

async function GetData(q, setDataArray) {
  const result = await getDocs(q)
  setDataArray([])
  result.forEach((doc) => {
    if (doc.exists) {
      var id = doc.id
      var data = doc.data()
      if (data.endDate.seconds === 0) {
        setDataArray((arr) => [
          ...arr,
          {
            ...data,
            startDate: new Date(
              data.startDate.seconds * 1000
            ).toLocaleDateString('ko-Kr'),
            endDate: '진행 중',
            id: id,
          },
        ])
      } else {
        setDataArray((arr) => [
          ...arr,
          {
            ...data,
            startDate: new Date(
              data.startDate.seconds * 1000
            ).toLocaleDateString('ko-Kr'),
            endDate: new Date(data.endDate.seconds * 1000).toLocaleDateString(
              'ko-Kr'
            ),
            id: id,
          },
        ])
      }
    } else {
      console.log('데이터없음')
    }
  })
}

const StyledBorder = styled.div`
  padding-top: 20px;
  margin-left: 20px;
  padding-bottom: 20px;
`
const StyledButton = styled.button`
  width: 5rem;
  height: 2rem;
`
