import React, { useEffect, useMemo, useState } from 'react'
import { useTable } from 'react-table'
import styled from 'styled-components'
import FinishModal from './FinishModal'
import ReactModal from 'react-modal'

function ShowTable(props) {
  const [IsModalOpen, setIsModalOpen] = useState(false)
  const [IsFinishModalOpen, setFinishModalOpen] = useState(false)
  const [values, setValues] = useState()
  const [disabled, setDisabled] = useState(false)
  useEffect(() => {
    setDisabled(props.fridgeNumber === '')
  }, [])

  const columns = useMemo(
    () => [
      {
        Header: '시작일',
        accessor: 'startDate', // accessor is the "key" in the data
      },
      {
        Header: '종료일',
        accessor: 'endDate',
      },
      {
        Header: '냉장고번호',
        accessor: 'fridgeNum',
      },
      {
        Header: '층',
        accessor: 'machineNum',
      },
      {
        Header: '육종',
        accessor: 'species',
      },
      {
        Header: '부위',
        accessor: 'cut',
      },
      {
        Header: '등급',
        accessor: 'grade',
      },
      {
        Header: '숙성 전 무게(g)',
        accessor: 'beforeWeight',
      },
      {
        Header: '숙성 후 무게(g)',
        accessor: 'afterWeight',
      },
      {
        Header: '단가(원/㎏)',
        accessor: 'price',
      },
      // {
      //   Header: '숙성 종료하기',
      //   accessor: 'finish',
      //   Cell: (value) => {
      //     return (
      //       <div>
      //         <StyledButton
      //           onClick={() => handleModalOpenClick(value)}
      //           disabled={disabled}
      //         >
      //           숙성종료
      //         </StyledButton>
      //       </div>
      //     )
      //   },
      // },
    ],
    []
  )
  const data = useMemo(() => props.data, [props.data])
  console.log(data)
  const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } =
    useTable({ columns, data })

  function handleClickSubmit() {
    props.onRefresh()
    setIsModalOpen(false)
    setFinishModalOpen(true)
  }

  function handleClickCancel() {
    setIsModalOpen(false)
  }
  function handleFinishModalClickCancel() {
    setFinishModalOpen(false)
  }

  function handleModalOpenClick(value) {
    setValues(value.row.original)
    setIsModalOpen(true)
  }

  return (
    <div>
      <table {...getTableProps()} style={{ border: 'solid 1px blue' }}>
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th
                  {...column.getHeaderProps()}
                  style={{
                    padding: '5px 10px 5px 10px',
                    borderBottom: 'solid 3px salmon',
                    background: 'royalblue',
                    color: 'snow',
                    fontWeight: 'bold',
                    fontSize: '1.1rem',
                  }}
                >
                  {column.render('Header')}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {rows.map((row) => {
            prepareRow(row)
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map((cell) => {
                  return (
                    <td
                      {...cell.getCellProps()}
                      style={{
                        textAlign: 'center',
                        padding: '10px',
                        border: 'solid 1px gray',
                        background: 'lemonchiffon',
                      }}
                    >
                      {cell.render('Cell')}
                    </td>
                  )
                })}
              </tr>
            )
          })}
        </tbody>
      </table>
      <FinishModal
        data={values}
        isOpen={IsModalOpen}
        onSubmit={handleClickSubmit}
        onCancel={handleClickCancel}
      />
      <ReactModal
        style={{
          overlay: {
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: 'rgba(255, 255, 255, 0.75)',
          },
          content: {
            position: 'absolute',
            width: '30%',
            height: '20%',
            top: '30%',
            left: '30%',
            border: '1px solid #ccc',
            background: '#fff',
            overflow: 'auto',
            WebkitOverflowScrolling: 'touch',
            borderRadius: '4px',
            outline: 'none',
            padding: '20px',
          },
        }}
        isOpen={IsFinishModalOpen}
        onRequestClose={handleFinishModalClickCancel}
      >
        <h4>완료처리되었습니다</h4>
        <CloseButton onClick={handleFinishModalClickCancel}>확인</CloseButton>
      </ReactModal>
    </div>
  )
}

export default ShowTable

const StyledButton = styled.button`
  margin: 0.5rem 0.2rem 0.2rem;
`

const CloseButton = styled.button`
  width: 60%;
  height: 45%;
  margin: 0 20% 0 20%;
`
