import { doc, updateDoc } from 'firebase/firestore'
import React, { useState } from 'react'
import ReactModal from 'react-modal'
import { getFirestore } from 'firebase/firestore'
import initializeApp from '../Firebase'

const FinishModal = ({ data, isOpen, onSubmit, onCancel }) => {
  const [afterWeight, setAfterWeight] = useState(0)
  const db = getFirestore(initializeApp)

  const checkValues =
    data === undefined
      ? {
          fridgeNum: '',
          machineNum: '',
          species: '',
          cut: '',
          grade: '',
          freeze: '',
          where: '',
          country: '',
          startDate: '',
          beforeWeight: '',
          id: '',
          price: '',
        }
      : data

  const handleClickSubmit = async () => {
    if (afterWeight > 0) {
      await updateDoc(doc(db, 'AgingPlans', checkValues.id), {
        afterWeight: afterWeight,
        endDate: new Date(),
      }).then(() => {
        console.log('처리 완료')
        setAfterWeight(0)
        onSubmit()
      })
    }
  }

  const handleClickCancel = () => {
    onCancel()
  }

  const onWeightChange = (e) => {
    const limit = 6
    setAfterWeight(e.target.value.slice(0, limit))
  }
  return (
    <ReactModal
      style={{
        overlay: {
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundColor: 'rgba(255, 255, 255, 0.75)',
        },
        content: {
          position: 'absolute',
          width: '50%',
          height: '60%',
          top: '15%',
          left: '15%',
          right: 0,
          bottom: 0,
          border: '1px solid #ccc',
          background: '#fff',
          overflow: 'auto',
          WebkitOverflowScrolling: 'touch',
          borderRadius: '4px',
          outline: 'none',
          padding: '20px',
        },
      }}
      isOpen={isOpen}
      appElement={document.getElementById('root')}
      onRequestClose={handleClickCancel}
    >
      <div>
        <h1>[숙성 완료 처리하기]</h1>
        <h3>숙성시작일 : {checkValues.startDate}</h3>
        <h3>
          냉장고: {checkValues.fridgeNum} , 층: {checkValues.machineNum}
        </h3>
        <h3>
          {checkValues.species} , {checkValues.cut} , {checkValues.country} ,{' '}
          {checkValues.grade}
        </h3>
        <h3>숙성 전 무게 : {checkValues.beforeWeight}g</h3>
        <h3>Kg 단가 : {checkValues.price}원/㎏</h3>
        <input
          type='number'
          name='afterWeight'
          placeholder=''
          value={afterWeight || ''}
          onChange={onWeightChange}
        />
        <br />
        <>
          <button onClick={handleClickSubmit}>제출</button>
          <button onClick={handleClickCancel}>닫기</button>
        </>
      </div>
    </ReactModal>
  )
}

export default FinishModal
