import { React } from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import { useStoreContext } from './StoreContext'

function Header() {
  const { storeName } = useStoreContext()
  console.log('I' + storeName)
  return (
    <StyledBorder>
      <Link to='/'>
        <StyledH1>
          가이버스
          <br />
          SendPlans
        </StyledH1>
      </Link>
      <div style={{ display: `flex` }}>
        <Link to={`/create/${storeName}`}>
          <StyledButton>생성하러 가기</StyledButton>
        </Link>
        <Link to={`/finish/${storeName}`}>
          <StyledButton>끝장내러 가기</StyledButton>
        </Link>

        <Link to={`/all/${storeName}`}>
          <StyledButton>전부 조회</StyledButton>
        </Link>
      </div>

      <InsertStoreName storeName={storeName} />
    </StyledBorder>
  )
}

function InsertStoreName(props) {
  const storeName = props.storeName
  console.log('HI' + storeName)
  // const onChange = (e) => {
  //   props.setStoreName(e.target.value)
  // }
  return (
    <div>
      <h2>▶현재 매장명 : {storeName}</h2>
      {/* <StyledInput name='storeName' placeholder='매장명' onChange={onChange} /> */}
    </div>
  )
}
export default Header

const StyledBorder = styled.div`
  margin-left: 20px;
  padding-bottom: 20px;
  border-bottom: 0.1rem solid black;
  width: 40%;
`

const StyledButton = styled.button`
  width: 6rem;
  height: 4rem;
  font-size: 1.2rem;
  margin: 20px;
`

const StyledH1 = styled.h1`
  margin-top: 0px;
`
