import React from 'react'
import CreateProject from './CreateProject/CreateProject'
import FinishProject from './FinishProject/FinishProject'
import AllProjects from './AllProjects/AllProjects'
import NotFound from './NotFound'
import Main from './Main'
import { BrowserRouter, Routes, Route } from 'react-router-dom'
import styled from 'styled-components'
import Header from './Header'

function App() {
  return (
    <StyledDiv>
      <BrowserRouter>
        <Header />
        <Routes>
          <Route path='/' element={<Main />}></Route>
          <Route path='/create/:storename' element={<CreateProject />}></Route>
          <Route path='/finish/:storename' element={<FinishProject />}></Route>
          <Route path='/all/:storename' element={<AllProjects />}></Route>
          <Route path='*' element={<NotFound />}></Route>
        </Routes>
      </BrowserRouter>
    </StyledDiv>
  )
}

const StyledDiv = styled.div`
  min-height: 100vh;
  height: 100vh;
  padding-top: 30px;
  background-color: #a4cca3;
  overflow: auto;
`

export default App
